import { defaultOptions, defaultProps } from '../common/component-handler/press-component';
import { GRAY, RED } from '../common/constant/color';
import { toPromise } from '../common/format/function';
import { style } from '../common/utils/style';

import { nextTick } from '../common/utils/system';
import utils from '../common/utils/utils';
import { t } from '../locale';

import { button } from '../mixins/button';

import PressButton from '../press-button/press-button.vue';
import { getDialogMixin } from '../press-dialog/dialog-mixin';
import PressPopup from '../press-popup-plus/press-popup-plus.vue';


const props = {
  show: {
    type: Boolean,
    default: false,
  },
  title: { type: String, default: '' },
  message: { type: String, default: '' },

  useSlot: Boolean,
  useTitleSlot: Boolean,

  className: { type: String, default: '' },
  customStyle: { type: [String, Object], default: '' },

  asyncClose: Boolean,
  beforeClose: { type: [null, Function], default: null },

  theme: {
    type: String,
    default: 'default',
  },
  messageAlign: { type: String, default: '' },
  width: {
    type: [String, Number],
    default: '',
  },
  zIndex: {
    type: Number,
    default: 2000,
  },

  overlay: {
    type: Boolean,
    default: true,
  },
  overlayStyle: { type: String, default: '' },
  transition: {
    type: String,
    default: 'scale',
  },

  showConfirmButton: {
    type: Boolean,
    default: true,
  },
  showCancelButton: Boolean,

  confirmButtonText: {
    type: String,
    default: t('confirm'),
  },
  cancelButtonText: {
    type: String,
    default: t('cancel'),
  },
  confirmButtonColor: {
    type: String,
    default: RED,
  },
  cancelButtonColor: {
    type: String,
    default: GRAY,
  },

  closeOnClickOverlay: Boolean,
  confirmButtonOpenType: { type: String, default: '' },

  ...defaultProps,
};

export default {
  name: 'PressDialogPlus',
  options: {
    ...defaultOptions,
    styleIsolation: 'shared',
  },
  components: {
    PressPopup,
    PressButton,
  },
  mixins: [button, getDialogMixin(props)],
  props,
  emits: ['close', 'confirm', 'cancel'],
  data() {
    return {
      // ...getPropsData(this, props),

      utils,
      loading: {
        confirm: false,
        cancel: false,
      },
      callback: (() => { }),
    };
  },
  computed: {
    popupCustomStyle() {
      return style([
        {
          width: utils.addUnit(this.dataWidth),
        },
        this.dataCustomStyle,
      ]);
    },
  },
  watch: {
    // ...getPropsWatch(props),
    dataShow: {
      handler(val) {
        if (!val) {
          this.stopLoading();
        }
      },
    },
  },
  methods: {
    // setData(data) {
    //   setPropsToData.call(this, data);
    // },
    onConfirm() {
      this.handleAction('confirm');
    },
    onCancel() {
      this.handleAction('cancel');
    },
    onClickOverlay() {
      this.close('overlay');
    },
    close(action) {
      this.dataShow = false;
      nextTick(() => {
        this.$emit('close', action);
        const { callback } = this;
        if (callback) {
          callback(action, this);
        }
      });
    },
    stopLoading() {
      this.loading.confirm = false;
      this.loading.cancel = false;
    },
    handleAction(action) {
      this.$emit(action, { dialog: this });
      const { dataAsyncClose, dataBeforeClose } = this;
      if (!dataAsyncClose && !dataBeforeClose) {
        this.close(action);
        return;
      }

      this.loading[action] = true;

      if (dataBeforeClose) {
        toPromise(dataBeforeClose(action)).then((value) => {
          if (value) {
            this.close(action);
          } else {
            this.stopLoading();
          }
        });
      }
    },
  },
};
